/*
-------------------------------------------------------------------------------
Sidebar content
-------------------------------------------------------------------------------
*/

$sidebar-menu-vertical-margin: 2.5rem;

$sidebar-item-horizontal-spacing: 2.5rem;
$sidebar-item-vertical-spacing: 0.5rem;

$sidebar-icon-width-height: 3rem;

.sidebar-content {
	display: block;
	padding: 0;
	margin: $sidebar-menu-vertical-margin $sidebar-item-horizontal-spacing;
}

.sidebar-content>ul,
.sidebar-content>ol {
	margin-left: 0;
}

.sidebar-divider {
	padding: 0;
	margin: $sidebar-item-vertical-spacing $sidebar-item-horizontal-spacing;
	height: 1.5px;
	background-color: $border-color;
}

.sidebar-link {
	display: block;
	padding: $sidebar-item-vertical-spacing $sidebar-item-horizontal-spacing;
	margin: 0;
	min-height: 3rem;
	height: auto;
	color: $base-text-color-light;
	background-color: transparent;
	border: 0 solid transparent;
	border-radius: 0;
	text-decoration: none;


	&:hover {
		color: $text-color;
		background-color: transparent;
		border-color: transparent-hover;
		text-decoration: none;
	}

	&.active {
		background-color: transparent;
		border-color: transparent;
		font-weight: 500;
		// &:hover {
		// 	background-color: transparent-hover;
		// 	border-color: transparent-hover;
		// }
	}


	&.sidebar-link-indent {
		padding-left: 4rem;
		position: relative;

		&::before {
			content: '';
			position: absolute;
			left: 2.5rem;
			width: 8px;
			height: 8px;
			border-radius: 50%;
			background-color: #efefef;
			// box-shadow: $box-shadow;
			top: calc(50% - 4px);
			-webkit-transition: background-color .5s linear;
			-ms-transition: background-color .5s linear;
			transition: background-color .5s linear;

		}

		&.active::before,
		&:hover::before {
			background-color: var(--primary-color);
		}
	}

	&:not(.with-icon):not(.sidebar-link-indent) {
		border-left: 0px solid transparent;
		padding-left: 2.5rem;

		// -webkit-transition: border-width .3s linear, padding .3s linear;
		// -ms-transition: border-width .3s linear, padding .3s linear;
		// transition: border-width .3s linear, padding .3s linear;


		&.active {
			border-left-width: 8px;
			padding-left: calc(2.5rem - 8px);
			border-color: var(--primary-color);
		}

		// &:hover {
		// 	border-left-width: 4px;
		// 	padding-left: calc(2.5rem - 4px);
		// 	border-color: var(--primary-color);
		// }



		&.sidebar-link-primary {
			border-color: var(--primary-color);
			// color: var(--primary-color);
		}

		&.sidebar-link-muted {
			border-color: $muted-color;
			// color: $muted-color;
		}

		&.sidebar-link-success {
			border-color: var(--success-color);
			// color: var(--success-color);
		}

		&.sidebar-link-danger {
			border-color: var(--danger-color);
			// color: var(--danger-color);
		}


	}

	&.with-icon {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		align-items: center;
		padding: $sidebar-item-vertical-spacing $sidebar-item-horizontal-spacing;

		&>.sidebar-icon,
		&>i {
			display: -ms-flexbox;
			display: flex;
			-ms-flex-align: center;
			align-items: center;
			-ms-flex-pack: center;
			justify-content: center;
			width: $sidebar-icon-width-height;
			height: $sidebar-icon-width-height;
			font-size: 1.6rem;
			margin-right: 1rem;
			color: $base-text-color-light;
			background-color: rgba(0, 0, 0, 0.05);
			border: 2px solid transparent;
			border-radius: 50%;
// 			width: 3rem;
// text-align: center;
		}

		&:hover {

			&>.sidebar-icon,
			&>i {
				border-color: $text-color;
			}
		}

		&.active {

			.sidebar-icon,
			i {
				background-color: $text-color;
				border-color: transparent;
				// box-shadow: 2px 2px 0 rgba(32, 33, 36, .28);
				// box-shadow: 2px 2px 0 rgba(32, 33, 36, .28);
			}
		}

		&.sidebar-link-primary {

			.sidebar-icon,
			i {
				color: var(--primary-color);
			}

			&.active {

				.sidebar-icon,
				i {
					background-color: var(--primary-color);
				}
			}

			&:hover {

				.sidebar-icon,
				i {
					border-color: var(--primary-color);
				}
			}
		}

		&.sidebar-link-muted {

			.sidebar-icon,
			i {
				color: $muted-color;
			}

			&.active {

				.sidebar-icon,
				i {
					background-color: $muted-color;
				}
			}

			&:hover {

				.sidebar-icon,
				i {
					border-color: $muted-color;
				}
			}
		}

		&.sidebar-link-success {

			.sidebar-icon,
			i {
				color: var(--success-color);
			}

			&.active {

				.sidebar-icon,
				i {
					background-color: var(--success-color);
				}
			}

			&:hover {

				.sidebar-icon,
				i {
					border-color: var(--success-color);
				}
			}
		}

		&.sidebar-link-danger {

			.sidebar-icon,
			i {
				color: var(--danger-color);
			}

			&.active {

				.sidebar-icon,
				i {
					background-color: var(--danger-color);
				}
			}

			&:hover {

				.sidebar-icon,
				i {
					border-color: var(--danger-color);
				}
			}
		}

		&.active {

			.sidebar-icon,
			i {
				color: white;
			}

		}
	}
}

.sidebar-title {
	text-transform: uppercase;
	padding: 0;
	margin: 2.5rem 2.5rem 0.5rem;
	font-size: 1.4rem;
	font-weight: normal;
	color: $border-color;
	border-bottom: 1.5px solid $border-color;
}

.sidebar-brand {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	align-items: center;
	padding: 0;
	margin: $sidebar-item-vertical-spacing $sidebar-item-horizontal-spacing;
	font-size: $navbar-brand-font-size;
	font-weight: $navbar-brand-font-weight;
	color: $text-color;

	&:hover {
		color: $base-text-color-light;
		text-decoration: none;
	}

	img {
		// height: $navbar-brand-image-height;
		margin-right: $navbar-brand-image-margin-right;
		width: 100%;
	}
}

.mobile-nav {
	display: none;
	margin-bottom: 1.5rem;
	padding-bottom: 1.5rem;
	border-bottom: 1px solid $border-color;

}



@include respond-below(sm) {

	.mobile-nav {
		display: block;
	}
}

.sidebar-topnav {
	position: absolute;
	top: 1rem;
	left: 0;
	text-align: center;
	width: 100%;

	ul {
		padding: 0;
		list-style-type: none;
		white-space: nowrap;
		overflow: hidden;

		li {
			display: inline-block;
			padding: 0 16px;
			border-left: 1px solid #cbcbcb;
			text-align: center;

			&:first-child {
				border-left: none;
			}

			a {
				display: block;
				color: #cbcbcb;
				text-transform: uppercase;
				font-size: 12px;
				font-size: 1.0rem;

				&:hover {
					color: var(--primary-color);
					text-decoration: none;
				}
			}

			i {
				display: block;
				padding-top: 3px;
				margin-bottom: 9px;
				font-size: 24px;
				font-size: 2rem;
			}
		}
	}
}

.spacer {
	height: 2rem;
}