/*
-------------------------------------------------------------------------------
2.  Typography [hm-02]
-------------------------------------------------------------------------------
*/

body {
    /* DSMZ font Rubik */
    font-family: $font-family;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-smoothing: antialiased;
}

.lead {
    font-size: 1.8rem
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 2rem;
    margin-bottom: 1.5rem;
    font-weight: 600;
}
h1 {
    font-size: 2.5rem;
    line-height: 1.3;
}
h2 {
    font-size: 2.2rem;
    line-height: 1.35;
}
h3 {
    font-size: 2rem;
    line-height: 1.4;
}
h4 {
    font-size: 1.8rem;
    line-height: 1.45;
}
h5 {
    font-size: 1.7rem;
}
h6 {
    font-size: 1.5rem;
}

b {
    font-weight: 500;
}

blockquote {
    margin: 0;
    padding: 1rem 2rem;
    font-style: italic;
    border-left: 6px solid var(--primary-color);
    p {
        &:first-child {
            margin-top: 0;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.section-title {
    font-weight: 500;
    font-size: 2.5rem;
    .section-icon {
        height: 22px;
        vertical-align: middle;
      }
}

.icon-rounded {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background-color: $gray-color-light;
    display: inline-block;
vertical-align: middle;
text-align: center;
line-height: 3rem;
margin-right: 1rem;
color: $text-color;
}