/*
-------------------------------------------------------------------------------
Badges
-------------------------------------------------------------------------------
*/

.badge {
    display: inline-block;
    position: relative;
    font-size: 1.2rem;
    line-height: 1.2;
    padding: 0.2rem 0.8rem;
    color: $text-color;
    background-color: #ffffff;
    border: 2px solid $border-color;
    border-radius: $border-radius;

    @each $name in $colors {
        &.badge-#{$name} {
            color: var(--#{$name}-color);
            border-color: var(--#{$name}-color);

            &.filled {
                color: white;
                background-color: var(--#{$name}-color);
            }
        }
    }

    &.pill {
        border-radius: 3rem;
    }
}

.badge-pill {
    border-radius: 3rem;
}

/* Badge group */

.badge-group {
    display: -ms-inline-flexbox;
    display: inline-flex;
}

.badge-group>.badge:not(:first-child) {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.badge-group>.badge:not(:last-child) {
    border-right: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}


a.badge:hover,
a.badge-group:hover {
    text-decoration: none;
}

a.badge:hover,
a.badge-group:hover>.badge {
    color: inherit;
    background-color: $gray-color-very-light;
}

@each $name in $colors {

    a.badge.badge-#{$name}:hover,
    a.badge-group:hover>.badge.badge-#{$name} {
        color: var(--#{$name}-color-dark);
        background-color: var(--#{$name}-color-very-light);
    
    }
}
